import React from 'react';
import {
    GraduationCap, Users, TrendingUp, TrendingDown,
    CreditCard, Calendar as CalendarIcon, BadgeCheck, DollarSign, Plus
} from 'lucide-react';
import {
    BarChart, Bar, XAxis, YAxis, CartesianGrid, Tooltip, ResponsiveContainer, Legend,
    PieChart, Pie, Cell
} from 'recharts';
import * as Types from '@/lib/types';
import * as Utils from '@/lib/utils';
import { Card } from '@/components/ui/card';

interface DashboardViewProps {
    students: Types.Student[];
    teachers: Types.Teacher[];
    staff: Types.Staff[];
    payments: Types.Payment[];
    expenses: Types.Expense[];
    fees: Types.FeeStructure[];
    classes: Types.Class[];
    settings: Types.Settings;
    onChangeView: (view: Types.ViewState) => void;
}

export const DashboardView: React.FC<DashboardViewProps> = ({
    students, teachers, staff, payments, expenses, fees, classes, settings, onChangeView
}) => {
    // Calculations
    const totalStudents = students.length;
    const totalTeachers = teachers.length;
    const totalStaff = staff.length;

    // Financials (Current Session/Term context)
    const currentSessionPayments = payments.filter(p => p.session === settings.current_session && p.term === settings.current_term);
    const totalRevenue = currentSessionPayments.reduce((acc, p) => acc + p.amount, 0);

    const currentSessionExpenses = expenses.filter(e => e.session === settings.current_session && e.term === settings.current_term);
    const totalExpenses = currentSessionExpenses.reduce((acc, e) => acc + e.amount, 0);

    // Expected Revenue calculation
    let totalExpectedRevenue = 0;
    students.forEach(s => {
        const { totalBill } = Utils.getStudentBalance(s, fees, [], settings.current_session, settings.current_term); // Pass empty payments to just get bill
        totalExpectedRevenue += totalBill;
    });

    const financeData = [
        { name: 'Income', amount: totalRevenue, fill: '#10b981' }, // emerald-500
        { name: 'Expenses', amount: totalExpenses, fill: '#ef4444' }, // red-500
    ];
    const genderData = [
        { name: 'Male', value: students.filter(s => s.gender === 'Male').length, color: '#3b82f6' },
        { name: 'Female', value: students.filter(s => s.gender === 'Female').length, color: '#ec4899' },
    ];

    return (
        <div className="space-y-8">
            {/* Welcome */}
            <div className="flex flex-col md:flex-row justify-between items-start md:items-center gap-4">
                <div>
                    <h1 className="text-3xl font-bold text-gray-900 tracking-tight">Executive Dashboard</h1>
                    <p className="text-gray-500 font-medium mt-1">Overview for {settings.current_session} • {settings.current_term}</p>
                </div>
                <div className="bg-white px-5 py-3 rounded-xl shadow-sm border border-gray-100 hidden md:block">
                    <span className="text-xs text-gray-400 uppercase font-bold tracking-wider">Net Position</span>
                    <div className={`text-2xl font-bold ${totalRevenue - totalExpenses >= 0 ? 'text-emerald-600' : 'text-red-600'}`}>
                        {Utils.formatCurrency(totalRevenue - totalExpenses)}
                    </div>
                </div>
            </div>

            {/* Colorful Stats Grid */}
            <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-6">
                <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-blue-500 to-blue-600 p-6 text-white shadow-lg transition-transform hover:scale-[1.02]">
                    <div className="relative z-10 flex justify-between items-start">
                        <div>
                            <p className="text-blue-100 text-sm font-medium">Total Students</p>
                            <h3 className="text-3xl font-bold mt-2">{totalStudents}</h3>
                            <p className="text-xs text-blue-200 mt-1">Active Enrollment</p>
                        </div>
                        <div className="p-2 bg-white/20 rounded-lg backdrop-blur-sm"><GraduationCap className="h-6 w-6 text-white" /></div>
                    </div>
                    <div className="absolute -bottom-4 -right-4 h-24 w-24 rounded-full bg-white/10 blur-xl"></div>
                </div>
                <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-purple-500 to-purple-600 p-6 text-white shadow-lg transition-transform hover:scale-[1.02]">
                    <div className="relative z-10 flex justify-between items-start">
                        <div>
                            <p className="text-purple-100 text-sm font-medium">Total Staff</p>
                            <h3 className="text-3xl font-bold mt-2">{totalTeachers + totalStaff}</h3>
                            <p className="text-xs text-purple-200 mt-1">{totalTeachers} Teachers, {totalStaff} Non-Academic</p>
                        </div>
                        <div className="p-2 bg-white/20 rounded-lg backdrop-blur-sm"><Users className="h-6 w-6 text-white" /></div>
                    </div>
                    <div className="absolute -bottom-4 -right-4 h-24 w-24 rounded-full bg-white/10 blur-xl"></div>
                </div>
                <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-emerald-500 to-emerald-600 p-6 text-white shadow-lg transition-transform hover:scale-[1.02]">
                    <div className="relative z-10 flex justify-between items-start">
                        <div>
                            <p className="text-emerald-100 text-sm font-medium">Revenue (Term)</p>
                            <h3 className="text-3xl font-bold mt-2">{Utils.formatCurrency(totalRevenue)}</h3>
                            <p className="text-xs text-emerald-200 mt-1">Target: {Utils.formatCurrency(totalExpectedRevenue)}</p>
                        </div>
                        <div className="p-2 bg-white/20 rounded-lg backdrop-blur-sm"><TrendingUp className="h-6 w-6 text-white" /></div>
                    </div>
                    <div className="absolute -bottom-4 -right-4 h-24 w-24 rounded-full bg-white/10 blur-xl"></div>
                </div>
                <div className="relative overflow-hidden rounded-2xl bg-gradient-to-br from-rose-500 to-rose-600 p-6 text-white shadow-lg transition-transform hover:scale-[1.02]">
                    <div className="relative z-10 flex justify-between items-start">
                        <div>
                            <p className="text-rose-100 text-sm font-medium">Expenses (Term)</p>
                            <h3 className="text-3xl font-bold mt-2">{Utils.formatCurrency(totalExpenses)}</h3>
                            <p className="text-xs text-rose-200 mt-1">{currentSessionExpenses.length} Transactions</p>
                        </div>
                        <div className="p-2 bg-white/20 rounded-lg backdrop-blur-sm"><TrendingDown className="h-6 w-6 text-white" /></div>
                    </div>
                    <div className="absolute -bottom-4 -right-4 h-24 w-24 rounded-full bg-white/10 blur-xl"></div>
                </div>
            </div>

            {/* Charts & Actions */}
            <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
                <div className="lg:col-span-2 space-y-6">
                    <Card title="Financial Overview">
                        <div className="h-72 w-full min-h-[300px]">
                            <ResponsiveContainer width="100%" height="100%">
                                <BarChart data={financeData}>
                                    <CartesianGrid strokeDasharray="3 3" vertical={false} stroke="#e5e7eb" />
                                    <XAxis dataKey="name" axisLine={false} tickLine={false} tick={{ fill: '#6b7280', fontSize: 12 }} dy={10} />
                                    <YAxis axisLine={false} tickLine={false} tickFormatter={(val) => `₦${val / 1000}k`} tick={{ fill: '#6b7280', fontSize: 12 }} />
                                    <Tooltip formatter={(value) => [Utils.formatCurrency(value as number), 'Amount']} />
                                    <Bar dataKey="amount" radius={[6, 6, 0, 0]} barSize={60}>
                                        {financeData.map((entry, index) => <Cell key={`cell-${index}`} fill={entry.fill} />)}
                                    </Bar>
                                </BarChart>
                            </ResponsiveContainer>
                        </div>
                    </Card>

                    <Card title="Quick Actions">
                        <div className="grid grid-cols-2 md:grid-cols-4 gap-4">
                            <button onClick={() => onChangeView('students')} className="group p-4 bg-white border border-blue-100 hover:border-blue-300 hover:shadow-md rounded-xl flex flex-col items-center justify-center gap-3 transition-all">
                                <div className="p-3 bg-blue-100 text-blue-600 rounded-full group-hover:scale-110 transition-transform"><Plus className="h-6 w-6" /></div>
                                <span className="font-semibold text-gray-700 group-hover:text-blue-700">New Student</span>
                            </button>
                            <button onClick={() => onChangeView('fees')} className="group p-4 bg-white border border-emerald-100 hover:border-emerald-300 hover:shadow-md rounded-xl flex flex-col items-center justify-center gap-3 transition-all">
                                <div className="p-3 bg-emerald-100 text-emerald-600 rounded-full group-hover:scale-110 transition-transform"><CreditCard className="h-6 w-6" /></div>
                                <span className="font-semibold text-gray-700 group-hover:text-emerald-700">Record Fee</span>
                            </button>
                            <button onClick={() => onChangeView('attendance')} className="group p-4 bg-white border border-rose-100 hover:border-rose-300 hover:shadow-md rounded-xl flex flex-col items-center justify-center gap-3 transition-all">
                                <div className="p-3 bg-rose-100 text-rose-600 rounded-full group-hover:scale-110 transition-transform"><CalendarIcon className="h-6 w-6" /></div>
                                <span className="font-semibold text-gray-700 group-hover:text-rose-700">Attendance</span>
                            </button>
                            <button onClick={() => onChangeView('id_cards')} className="group p-4 bg-white border border-orange-100 hover:border-orange-300 hover:shadow-md rounded-xl flex flex-col items-center justify-center gap-3 transition-all">
                                <div className="p-3 bg-orange-100 text-orange-600 rounded-full group-hover:scale-110 transition-transform"><BadgeCheck className="h-6 w-6" /></div>
                                <span className="font-semibold text-gray-700 group-hover:text-orange-700">ID Cards</span>
                            </button>
                        </div>
                    </Card>
                </div>
                <div className="space-y-6">
                    <Card title="Student Population">
                        <div className="h-56 w-full relative min-h-[224px]">
                            <ResponsiveContainer width="100%" height="100%">
                                <PieChart>
                                    <Pie data={genderData} cx="50%" cy="50%" innerRadius={60} outerRadius={80} paddingAngle={5} dataKey="value">
                                        {genderData.map((entry, index) => <Cell key={`cell-${index}`} fill={entry.color} strokeWidth={0} />)}
                                    </Pie>
                                    <Tooltip />
                                </PieChart>
                            </ResponsiveContainer>
                            <div className="absolute inset-0 flex flex-col items-center justify-center pointer-events-none">
                                <span className="text-3xl font-bold text-gray-900">{totalStudents}</span>
                                <span className="text-xs text-gray-500 font-medium uppercase">Students</span>
                            </div>
                        </div>
                    </Card>
                    <Card title="Recent Transactions">
                        <div className="space-y-4 mt-2">
                            {payments.slice(-4).reverse().map(p => {
                                const s = students.find(std => std.id === p.student_id);
                                return (
                                    <div key={p.id} className="flex items-center gap-3 pb-3 border-b border-gray-100 last:border-0 last:pb-0">
                                        <div className="h-10 w-10 rounded-full bg-emerald-50 text-emerald-600 flex items-center justify-center flex-shrink-0 border border-emerald-100"><DollarSign className="h-5 w-5" /></div>
                                        <div className="min-w-0 flex-1">
                                            <p className="text-sm font-semibold text-gray-900 truncate">{s?.names || 'Unknown Student'}</p>
                                            <p className="text-xs text-gray-500">School Fees Payment</p>
                                        </div>
                                        <div className="text-sm font-bold text-emerald-600">+{Utils.formatCurrency(p.amount)}</div>
                                    </div>
                                )
                            })}
                            {payments.length === 0 && <p className="text-gray-400 text-sm text-center py-4 italic">No recent payments recorded.</p>}
                        </div>
                    </Card>
                </div>
            </div>
        </div>
    );
};
